.meals {
    max-width: 60rem;
    width: 70%;
    margin: 2rem auto;
    animation: meals-appear 1s ease-out forwards;
  }
  
  .meals ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  @keyframes meals-appear {
    from {
      opacity: 0;
      transform: translateY(3rem);
    }
  
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  @media only screen and (max-width: 414px){
    .meals {
      max-width: 60rem;
      width: 100%;
      margin: 2rem auto;
      animation: meals-appear 1s ease-out forwards;
    }
  }