.nav {
  display: flex;
  flex: 1;
  max-width: 50%;
  justify-content: space-around;
}

.cart button {
  background-color: transparent;
  cursor: pointer;
  border-color: transparent;
  padding: 0.2rem 0.5rem;
  color: white;
  font-weight: bold;
  font-size: 15px;
}

.cartIcon {
  cursor: pointer;
  font: inherit;
  border: none;
  background-color: #4d1601;
  color: white;
  padding: 0.75rem 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.cartIcon:hover {
  background-color: #671e01;
  transition: all ease-out 0.3s;
}

.badge {
  background-color: red;
  padding: 0.25rem 1rem;
  border-radius: 25px;
  margin-left: 1rem;
  font-weight: bold;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.1rem;
}

.bump {
  animation: bump 600ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(0.9);
  }

  30% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(1.15);
  }

  100% {
    transform: scale(1);
  }
}

@media only screen and (max-width: 414px) {
  .nav {
    display: flex;
    flex: 1;
    max-width: 100%;
    justify-content: space-evenly;
  }

  .cartIcon {
    padding: 0.4rem 0.2rem;
    border-radius: 5px;
  }

  .icon {
    width: 1.05rem;
    height: 1.05rem;
    margin-right: 0.01rem;
  }

  .badge {
    padding: 0.05rem 0.5rem;
    margin-left: 0.2rem;
  }

  .cart button {
    padding: 0.1rem 0.2rem;
    font-size: 12px;
  }
}